import React from 'react';
import {inject} from "mobx-react";
import {GameUIStore} from "../../stores/GameUIStore";
import {PopupNames} from "../../utils/constants";

type PopupPureProps = {
    name: PopupNames;
    isActive?: boolean;
    children: () => React.ReactElement;
};

export function PopupRendererPure(
    {
        isActive,
        children,
    }: PopupPureProps,
) {
    return isActive ? children() : null;
}

type SceneInjectorStores = { gameUIStore: GameUIStore };

export const PopupRenderer = inject(
    ({ gameUIStore }: SceneInjectorStores, { name }) => ({
        isActive: gameUIStore.visiblePopup === name,
    }),
)(PopupRendererPure);
