import {action, observable} from "mobx";
import * as _ from 'lodash';
import {BoxInfo, GameRecord, PlayerScoreByPosition, PositionType} from "@web3game/typings/openBox";

import {EventEmitter} from "../../common/utils/EventEmitter";
import {OpenBoxTimeSpan, OpeningDuration, PrepareBoxTimeSpan} from "../core/openBoxConstants";


import {getSortedBoxes, playSound, Sounds} from "../core/gameHelper";
import {openBoxEvents} from "../eventBus/openBoxEvents";

/**
 * @typedef {Object} GameRecord
 * @property {[Number]} scores
 * @property {[Number]} sequencs
 * @property {[Number]} playerScores
 * */



const getInitialScores = (): PlayerScoreByPosition => ({
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
});

export class GamePlayerStore extends EventEmitter {

    public EventKeys = {
        start: 'start',
        end: 'end',
        pause: 'pause',
        resume: 'resume',
        beginOpenBox: 'beginOpenBox',
        finishOpenBox: 'finishOpenBox',
    };

    @observable
    public betAddress = '';

    @action
    public setBetAddress(betAddress: string) {
        this.betAddress = betAddress;
    }

    @observable
    public gameRecord: GameRecord | null = null;

    @observable
    public openIndex = -1;

    @observable
    public finishedOpenIndex: number = -1;

    @observable
    public openingBox: BoxInfo | null | undefined = null;

    @observable
    public isPaused = false;

    private playerTimeout = 0;

    @observable
    public playerScores: PlayerScoreByPosition = getInitialScores();

    @observable
    public boomedHeroId = null;

    @observable
    public fasterTimes = 1; // 


    /**
     * @param {CurrentGameRecord} gameRecord
     * */
    @action
    public startGame(gameRecord: GameRecord): Promise<any> {
        this.resetPlayer();
        if (!gameRecord) return Promise.resolve();

        if (gameRecord.play === false) {
            this.isPaused = true;
            openBoxEvents.triggerGameReadyStatus(false);
        } else {
            openBoxEvents.triggerGameReadyStatus(true);
        }

        this.gameRecord = gameRecord;

        this.emit(this.EventKeys.start);

        return Promise.resolve(
            this.playBoxByIndex(0),
        ).then((isEnd) => {
            if (isEnd && !this.isPaused) {
                this.emit(this.EventKeys.end);
                playSound(Sounds.result);
            }
        });
    }

    @action
    public restartGame() {
        if (this.gameRecord) {
            this.startGame(this.gameRecord);
        }
    }

    @action
    private clearPlayerTimeout() {
        if (this.playerTimeout) {
            clearTimeout(this.playerTimeout);
            this.playerTimeout = 0;
        }
    }

    @action
    public playBoxByIndex(openIndex: number): any {
        if (!this.gameRecord) {
            return false;
        }

        this.openIndex = openIndex;

        this.openingBox = null;

        const sortedBoxes = getSortedBoxes(this.gameRecord);

        const getBoxByOpenIndex = (index: number) => _.find(
            sortedBoxes,
            box => box.openIndex === index,
        );

        if (openIndex < sortedBoxes.length && openIndex >= 0) {

            return this.waitTimeout(
                action('onPrepareBox', () => {
                    const openingBox = getBoxByOpenIndex(openIndex);

                    this.openingBox = openingBox;

                    this.emit(this.EventKeys.beginOpenBox, openIndex, openingBox);

                    return this.waitTimeout(
                        action('onBoxOpen', () => {

                            if (this.isPaused) {
                                return false;
                            }


                            this.finishedOpenIndex = openIndex;

                            this.emit(this.EventKeys.finishOpenBox, openIndex, openingBox);

                            if (openingBox) {
                                const heroPos = openingBox.heroPos as PositionType;

                                if (openingBox.score === 0) {
                                    this.playerScores[heroPos] = 0;
                                    this.boomedHeroId = openingBox.heroId;
                                    playSound(Sounds.boom);
                                } else {
                                    playSound(Sounds.addScore);
                                    const oldScore = this.playerScores[heroPos];
                                    this.playerScores[heroPos] = oldScore
                                        + openingBox.score;
                                    this.boomedHeroId = null;
                                }
                            }

                            return this.waitTimeout(
                                () => this.playBoxByIndex(openIndex + 1),
                                OpeningDuration,
                            );

                        }),
                        OpenBoxTimeSpan,
                    );
                }),
                PrepareBoxTimeSpan,
            );


        }

        this.openIndex = -1;
        this.openingBox = null;

        return true;

    }

    @action
    public clearBoomer() {
        this.boomedHeroId = null;
    }

    public waitTimeout(actionFn: () => any, timeout = OpeningDuration) {
        return new Promise((resolve) => {
            // @ts-ignore
            this.playerTimeout = setTimeout(() => resolve(actionFn()), timeout * this.fasterTimes);
        });
    }

    @action
    public toggleFaster() {
        if (this.fasterTimes === 1) {
            this.fasterTimes = 0.2;
        } else {
            this.fasterTimes = 1;
        }
    }

    @action
    public pause = () => {
        this.isPaused = true;
        this.clearPlayerTimeout();
    };

    @action
    public resume = () => {
        this.isPaused = false;
        this.playBoxByIndex(this.openIndex);
    };

    @action
    public resetPlayer() {
        this.fasterTimes = 1;
        this.gameRecord = null;
        this.openingBox = null;
        this.openIndex = -1;
        this.finishedOpenIndex = -1;
        this.isPaused = false;
        this.boomedHeroId = null;
        this.playerScores = getInitialScores();
        this.clearPlayerTimeout();
    }

    @action
    public quit() {
        this.resetPlayer();
        this.removeAll();
    }

    /** @param {function(openIndex: Number, boxInfo: BoxInfo)} fn */
    public onBeginOpenBox(fn: () => void) {
        return this.on(this.EventKeys.beginOpenBox, fn);
    }

    /** @param {function(openIndex: Number, boxInfo: BoxInfo)} fn */
    public onFinishOpenBox(fn: () => void) {
        return this.on(this.EventKeys.finishOpenBox, fn);
    }

    public onStart(fn: () => void) {
        return this.on(this.EventKeys.start, fn);
    }

    public onGameEnd(fn: () => void) {
        return this.on(this.EventKeys.end, fn);
    }

    public onPause(fn: () => void) {
        return this.on(this.EventKeys.pause, fn);
    }

    public onResume(fn: () => void) {
        return this.on(this.EventKeys.resume, fn);
    }

}

export const gamePlayerStore = new GamePlayerStore();
