import {EventEmitter} from "../../common/utils/EventEmitter";

const ON_GAME_NOT_READY = 'ON_GAME_NOT_READY';

export class GlobalEvent extends EventEmitter {

    public onGameReadyStatusChange = (fn: () => void) => this.on(ON_GAME_NOT_READY, fn);

    public triggerGameReadyStatus = (isReady: boolean) => this.emit(ON_GAME_NOT_READY, isReady);

}

export const openBoxEvents = new GlobalEvent({});

export default openBoxEvents;
