import React, {useCallback} from 'react';
import {inject} from "mobx-react";
import {getLang} from "@web3game/lang";
import {Popup} from "./popups/Popup";
import {AccountStore} from "../stores/AccountStore";
import {GameUIStore} from '../stores/GameUIStore';

type AuthFormPureProps = {
    onSubmit: (params: any) => void;
    account?: string;
    ttBalance?: any;
    tokenBalance?: any;
    gas?: any;
    money?: number;
};

export function AuthFormPure(
    {
        onSubmit,
        account,
        ttBalance,
        tokenBalance,
        money = 0,
    }: AuthFormPureProps,
) {

    const handleSubmit = useCallback(() => {
        onSubmit({
            account,
            money,
        });
    }, []);

    return (
        <Popup title={getLang().CONFIRM_SUBMIT} className="gameAuthFormPopup">
            <div className="gameAuthForm">
                <div className="gameAuthFormField">
                    {getLang().ACCOUNT}&nbsp;:&nbsp;
                    {account}
                </div>
                <div className="gameAuthFormField">
                    {getLang().TT_BALANCE}&nbsp;:&nbsp;
                    {ttBalance}
                </div>
                <div className="gameAuthFormField">
                    {getLang().TOKEN_BALANCE}&nbsp;:&nbsp;
                    {tokenBalance}
                </div>
                <hr />
                <div className="gameAuthFormField" style={{marginTop: '20px'}}>
                    <button type="submit" onClick={handleSubmit}>
                        {getLang().SUBMIT}
                    </button>
                </div>
            </div>
        </Popup>
    );
}

type AuthFormInjectedProps = {
    accountStore?: AccountStore;
    gameUIStore?: GameUIStore;
};


export const AuthForm = inject(
    (
        {
            accountStore,
            gameUIStore,
        }: AuthFormInjectedProps,
    ) => {
        if (!accountStore || !gameUIStore) {
            return {};
        }

        const { popupData: {money} } = gameUIStore;

        const { 
            account, 
            ttBalance, 
            tokenBalance, 
            gas, 
        } = accountStore;

        return { account, ttBalance, tokenBalance, gas, money };
    },
)(AuthFormPure);
