import React, {useEffect, useState} from "react";
import _ from 'lodash';
import {
    getAdjustedSizeInfo,
    getWinSize,
    InitialSizeInfo,
    PreferMode,
    ViewPortSizeContext,
} from "./viewPortSizeHelper";


export type ViewPortSizeProps = {
    children: React.ReactNode | React.ReactElement;

    perfectHeight: number;
    perfectWidth: number;

    preferMaxWidth?: number;
    preferMaxHeight?: number;

    preferMode?: PreferMode;
}

export const ViewPortSizeProvider = React.memo(
    (
        {
            preferMode = PreferMode.width,

            perfectHeight,
            perfectWidth,

            preferMaxHeight = 0,
            preferMaxWidth = 900,

            children,
        }: ViewPortSizeProps
    ) => {

        const [{ winHeight, winWidth }, setWinSize] = useState(getWinSize());

        useEffect(() => {
            window.addEventListener(
                'resize',
                () => {
                    setWinSize(getWinSize());
                }
            );
        }, []);

        const { Provider } = ViewPortSizeContext;

        const staticSizeInfo = {
            winHeight,
            winWidth,

            perfectHeight,
            perfectWidth,

            preferMaxWidth,
        };

        const adjustedSizeInfo = getAdjustedSizeInfo({
            ...staticSizeInfo,
            preferMode,
        });

        const sizeInfo = _.extend<InitialSizeInfo>({}, staticSizeInfo, adjustedSizeInfo);

        return (
            <Provider value={sizeInfo}>
                {children}
            </Provider>
        );
    },
);
