import React from 'react';
import {inject, observer} from "mobx-react";
import {PlayerInfo} from "@web3game/common/components";
import {GameRecord, PositionType} from "@web3game/typings/openBox";
import {getSortedHeroList} from "../core/gameHelper";
import {GamePlayerStore} from "../stores/GamePlayerStore";
import {BoxGrid} from "./BoxGrid";

export type BoxGameDeskProps = {
    gameRecord?: GameRecord;
    playerScores?: object;
    openIndex?: number;
};

export function BoxGameDeskPure(
    {
        gameRecord,
        playerScores,
        openIndex = -1,
    }: BoxGameDeskProps
) {
    const heroList = gameRecord ? getSortedHeroList(gameRecord) : [];
    const realTimeScore = (pos: PositionType) => {
        if (!gameRecord || !playerScores) {
            return 0;
        }

        return (playerScores as any)[pos];
    };

    return (
        <>
            {heroList.map(
                hero => (
                    <PlayerInfo
                        key={hero.id}
                        imageIndex={hero.imageIndex}
                        score={realTimeScore(hero.pos)}
                        pos={hero.pos}
                        isActive={openIndex % 4 === hero.index}
                    />
                ),
            )}

            <BoxGrid />
        </>
    );
}

export type BoxGameDeskInjectedProps = {
    gamePlayerStore?: GamePlayerStore;
};

export const BoxGameDesk = inject(
    ({ gamePlayerStore }: BoxGameDeskInjectedProps) => ({
        gameRecord: gamePlayerStore ? gamePlayerStore.gameRecord : null,
        playerScores: gamePlayerStore ? gamePlayerStore.playerScores : {},
        openIndex: gamePlayerStore ? gamePlayerStore.openIndex : -1,
    }),
)(observer(BoxGameDeskPure));
