import React, {useEffect, useState} from 'react';
import _ from 'lodash';
import posed, {PoseGroup} from 'react-pose';
import {inject} from "mobx-react";
import {GameUIStore} from "../../stores/GameUIStore";
import {PopupNames} from "../../utils/constants";

type PopupPureProps = {
    title: string;
    className?: string;
    onQuit?: () => void;
    children: React.ReactElement | React.ReactElement[];
};

const Modal = posed.div({
    enter: {
        y: 0,
        opacity: 1,
        delay: 300,
        transition: {
            y: { type: 'spring', stiffness: 1000, damping: 15 },
            default: { duration: 300 }
        }
    },
    exit: {
        y: 50,
        opacity: 0,
        transition: { duration: 150 }
    }
});

const Backdrop = posed.div({
    enter: { opacity: 0.8 },
    exit: { opacity: 0 }
});

export function PopupPure(
    {
        title,
        className = '',
        onQuit,
        children,
    }: PopupPureProps,
) {
    const [isVisible, setIsVisible] = useState(false);
    useEffect(() => {
        setIsVisible(true);
    }, []);

    return (
        <div className={`popup ${className}`}>
            <PoseGroup>
                {
                    isVisible && [
                        (
                            <Backdrop
                                key="popupBackdrop"
                                className="popupBackdrop"
                                onClick={onQuit}
                                onTouchEnd={onQuit}
                                onKeyDown={() => undefined}
                                role="button"
                                tabIndex={0}
                                aria-label="click to close popup"
                            />
                        ),
                        (
                            <Modal key="popupWrapper" className="popupWrapper">
                                <div className="popupContainer">
                                    <div className="popupHeader">
                                        <button
                                            onClick={onQuit}
                                            onTouchEnd={onQuit}
                                            type="button"
                                            className="popupQuitButton"
                                        >
                                            Quit
                                        </button>
                                        <h2>{title}</h2>
                                    </div>
                                    <div className="popupBody">
                                        <div className="popupContent">
                                            {children}
                                        </div>
                                    </div>
                                </div>
                            </Modal>
                        )
                    ]
                }
            </PoseGroup>
        </div>
    );
}

type PopupProps = { gameUIStore: GameUIStore };

export const Popup = inject(({ gameUIStore }: PopupProps, { onQuit }) => ({
    onQuit: _.debounce(() => {
        if (onQuit) {
            onQuit();
        }

        gameUIStore.setVisiblePopup(PopupNames.none);
    }, 50),
}))(PopupPure);
