import React, {useEffect} from 'react';
import {inject} from "mobx-react";
import _ from 'lodash';
import {Table} from "@web3game/common/components/Table";
import {getLang} from "@web3game/lang";
import {GameRecord} from "@web3game/typings/openBox";
import {useVPStyle} from "@web3game/common/components/viewPort/getElStyle";
import {Box} from "./Box";
import {getSortedBoxes} from "../core/gameHelper";
import {GamePlayerStore} from "../stores/GamePlayerStore";

const ColumnNum = 4;

const range4 = () => _.range(4);

type BoxGridProps = {
    gameRecord?: GameRecord;
};

export function BoxGridPure({ gameRecord }: BoxGridProps) {

    useEffect(() => {
        if (gameRecord && !gameRecord.play) {
            setTimeout(() => alert(getLang().GAME_IS_NOT_READY), 300);
        }
    }, [gameRecord && gameRecord.id]);

    const renderColumn = (rowIndex: number, columnIndex: number) => {
        const boxIndex = rowIndex * ColumnNum + columnIndex;
        const boxInfo = gameRecord ? getSortedBoxes(gameRecord)[boxIndex] : null;

        if (!boxInfo) {
            return (
                <td key={columnIndex} />
            );
        }

        return (
            <td key={columnIndex}>
                <Box
                    boxIndex={boxIndex}
                    openIndex={boxInfo.openIndex}
                    score={boxInfo.score}
                    boxCover={_.random(1, 3)}
                />
            </td>
        );
    };

    const renderRow = (rowIndex: number) => (
        <tr key={rowIndex}>
            {range4().map(colIndex => renderColumn(rowIndex, colIndex))}
        </tr>
    );

    const tableSize = useVPStyle({
        width: 320,
    });

    return (
        <Table
            className="boxGrid"
            style={tableSize}
        >
            {range4().map(renderRow)}
        </Table>
    );
}

type BoxGridInjectedStores = {
    gamePlayerStore?: GamePlayerStore;
};


export const BoxGrid = inject(
    ({ gamePlayerStore }: BoxGridInjectedStores) => (
        { gameRecord: gamePlayerStore && gamePlayerStore.gameRecord }
    ),
)(BoxGridPure);
