import {OpenBoxAPI} from './OpenBoxAPI';
import {isUsingMockAPI} from "../isUsingMockAPI";


export async function getOpenBoxAPI(): Promise<OpenBoxAPI> {

    if (isUsingMockAPI()) {
        return import('./OpenBoxAPIMock').then(m => m.default);
    }

    return import('./OpenBoxAPIReal').then(m => m.default);
}
