import React, {useCallback, useEffect} from 'react';
import {Page} from "react-onsenui";
import _ from 'lodash';
import {Provider} from "mobx-react";
import {hot} from "react-hot-loader";
import {
    GameLayout,
    HistoryListPopup,
    MoneySelector,
    PopupElement,
    Scene,
} from "@web3game/common/components";
import * as commonStores from '@web3game/common/stores';
import {GameUIScene, PopupNames} from "@web3game/common/utils/constants";
import {popPage} from "@web3game/common/utils/navigatorHelper";
import {BetEvent} from "@web3game/typings/openBox";
import {AuthForm} from "@web3game/common/components/AuthForm";
import {getCommonAPI} from "@web3game/apis/common/getCommonAPI";
import {getOpenBoxAPI} from "@web3game/apis/openbox/getOpenBoxAPI";
import '@web3game/common/styles/common.scss';
import {ViewPortSizeProvider} from "@web3game/common/components/viewPort/ViewPortSizeProvider";
import * as stores from './stores';
import './styles/openBox.scss';
import {GameResultPopup} from "./components/GameResultPopup";
import {BoxGameDesk} from "./components/BoxGameDesk";
import {getOpenBoxMoneyList} from "./core/functions/getOpenBoxMoneyList";
import {compose} from "@web3game/common/utils/compose";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";

// @ts-ignore
window.OpenBoxStores = {
    ...stores,
    ...commonStores,
};

// region store helpers
const { gamePlayerStore } = stores;
const { gameUIStore, accountStore } = commonStores;

const onShowHistory = () => {
    gameUIStore.setVisiblePopup(PopupNames.history);
};

const onPlayGame = (betEvent: BetEvent) => {
    (async () => {
        const openBoxAPI = await getOpenBoxAPI();

        const gameRecord = await openBoxAPI.loadGame(betEvent);
        gameUIStore.setVisiblePopup(PopupNames.none);
        gameUIStore.setScene(GameUIScene.gamePlayer);
        gamePlayerStore.startGame(gameRecord);

        // 
        gamePlayerStore.onGameEnd(() => gameUIStore.setVisiblePopup(PopupNames.gameResult));

    })();
};

const onQuitPlayer = () => {
    gamePlayerStore.quit();
    gameUIStore.setScene(GameUIScene.moneySelector);
};

const onQuitGameResult = () => {
    gameUIStore.setVisiblePopup(PopupNames.none);
    gameUIStore.setScene(GameUIScene.moneySelector);
    gamePlayerStore.quit();
};

const onSubmitAuth = (params: any) => {
    (async () => {
        const { submitGame } = await getCommonAPI();
        try {
            await submitGame({
                ...params,
                betType: 3,
                issueNo: 0,
                nums: [],
                amounts: [],
                betAddress: gamePlayerStore.betAddress,
            });
            accountStore.updateDataVersion();
        } catch (error) {
            alert(error);
        }
        gameUIStore.setVisiblePopup(PopupNames.none);
    })();

};

const onSelectMoney = (money: number) => {
    gameUIStore.setVisiblePopup(PopupNames.authForm, { money });
};

const prepareRecords = (records: any[], currentUserId: string): object[] => _.map(
    records,
    record => ({
        ...record,
        currentUserId,
        id: record.id || _.random(0),
    }),
);


// endregion

type OpenBoxGameProps = {
    dataVersion: string;
};

export function OpenBoxGame(
    {
        dataVersion,
    }: OpenBoxGameProps
) {

    useEffect(() => {
        (async () => {
            const { getHistoryList, getBetAddress } = await getOpenBoxAPI();
            const list = await getHistoryList();
            stores.historyStore.setDisplayList(prepareRecords(list, accountStore.account));
            const betAddress = await getBetAddress();
            gamePlayerStore.setBetAddress(betAddress);
        })();

        // unbind listeners of gamePlayerStore
        return () => {
            gamePlayerStore.quit();
        };
    }, [dataVersion]);

    const popups: PopupElement[] = [
        { name: PopupNames.history, renderer: () => <HistoryListPopup onPlayGame={onPlayGame} /> },
        { 
            name: PopupNames.authForm,
            renderer: () => (
                <AuthForm 
                    onSubmit={onSubmitAuth}
                />
            ), 
        },
        {
            name: PopupNames.gameResult,
            renderer: () => (<GameResultPopup onQuit={onQuitGameResult} />),
        },
    ];

    const onQuitGame = useCallback(() => popPage(), []);

    return (
        <Page className="openBoxGamePage">
            <ViewPortSizeProvider
                perfectHeight={1344}
                perfectWidth={750}
                preferMaxWidth={600}
            >
                <Provider {...commonStores} {...stores}>
                    <GameLayout
                        onShowHistory={onShowHistory}
                        onQuitGame={onQuitGame}
                        onQuitPlayer={onQuitPlayer}
                        popups={popups}
                    >

                        <Scene name={GameUIScene.moneySelector}>
                            {() => (
                                <MoneySelector
                                    moneyList={getOpenBoxMoneyList()}
                                    onSelect={onSelectMoney}
                                />
                            )}
                        </Scene>

                        <Scene name={GameUIScene.gamePlayer}>
                            {() => <BoxGameDesk />}
                        </Scene>
                    </GameLayout>
                </Provider>
            </ViewPortSizeProvider>
        </Page>
    );
}

export default compose<OpenBoxGameProps>(
    hot(module),
    React.memo,
    watchAccountChange
)(OpenBoxGame);
