import React, {Fragment, useState} from 'react';
import {PopupRenderer} from "./PopupRenderer";
import {PopupElement} from "..";
import {PopupNames} from "../../utils/constants";


export type PopupManagerProps = {
    popups?: PopupElement[];
};

const PopupManagerContext = React.createContext({});

function usePopupManager(popupsList: PopupElement[]) {
    const [popups, setPopups] = useState(popupsList);

    return {
        popups,
        showPopup: (popup: PopupElement) => {
            setPopups([
                ...popups,
                popup,
            ]);
        },
        closePopup: (name: PopupNames) => {
            setPopups(popups.filter((p: any) => p.name !== name));
        },
    };
}

export function PopupManager({ popups = [] }: PopupManagerProps) {

    const { showPopup, popups: popupList } = usePopupManager(popups);

    return (
        <PopupManagerContext.Provider value={{ showPopup }}>
            <Fragment>
                {
                    popupList.map(({ name, renderer }) => (
                        <PopupRenderer key={name} name={name}>
                            {renderer}
                        </PopupRenderer>
                    ))
                }
            </Fragment>
        </PopupManagerContext.Provider>
    );

}
