import React, {useContext} from 'react';
import {HistoryButton} from "./HistoryButton";
import {PopupNames} from "../../utils/constants";
import {QuitGameButton} from "./QuitGameButton";
import {PopupManager} from "../popups/PopupManger";
import {ViewPortSizeContext} from "../viewPort/viewPortSizeHelper";

import BackgroundUrl from '../../styles/images/beijingtu.png';
import {useVPStyle} from "../viewPort/getElStyle";

export type PopupElement = { name: PopupNames; renderer: () => React.ReactElement }

export type GameLayoutProps = {
    children?: React.ReactElement | React.ReactElement[];
    onShowHistory: () => void;
    onQuitGame?: () => void;
    onQuitPlayer?: () => void;
    popups?: PopupElement[];
};

export function GameLayout(
    {
        children,
        onShowHistory,
        popups = [],
        onQuitGame,
        onQuitPlayer,
    }: GameLayoutProps,
) {
    const {
        adjustedHeight,
        adjustedWidth,
    } = useContext(ViewPortSizeContext);

    const applicationSize = {
        width: adjustedWidth,
        height: adjustedHeight,
    };

    const gameContentStyle = useVPStyle({
        paddingTop: 460,
    });

    return (
        <div className="gameApplication" style={applicationSize}>
            <img src={BackgroundUrl} alt="background" className="backgroundImage" />
            <HistoryButton onShowHistory={onShowHistory} />
            <QuitGameButton
                onQuitGame={onQuitGame}
                onQuitPlayer={onQuitPlayer}
            />
            <div className="gameDesktop">
                <div className="gameDesktopContent" style={gameContentStyle}>
                    {children}
                </div>
            </div>

            <PopupManager popups={popups} />
        </div>
    );
}

export default React.memo(GameLayout);
