import React from 'react';
import {useVPStyle} from "./viewPort/getElStyle";

type MoneySelectorPureProps = {
    moneyList: any[];
    onSelect: (money: number) => void;
};

export function MoneySelectorPure(
    {
        moneyList,
        onSelect,
    }: MoneySelectorPureProps,
) {
    const buttonSize = useVPStyle({
        width: 180,
        height: 90,
        fontSize: 24,
    });

    return (
        <div className="moneySelector">
            {moneyList.map(({ money, text }) => (
                <div key={money}>
                    <button
                        type="button"
                        className={`moneySelectorButton moneySelectorButton_${money}`}
                        style={buttonSize}
                        onClick={() => onSelect(money)}
                    >
                        {text}
                    </button>
                </div>
            ))}
        </div>
    );
}

export const MoneySelector = React.memo(MoneySelectorPure);
