import React, {CSSProperties, useEffect, useState} from 'react';
import _ from 'lodash';
import {hot} from "react-hot-loader";
import {PositionType} from "@web3game/typings/openBox";
import posed, {PoseGroup} from 'react-pose';
import {useVPStyle} from "./viewPort/getElStyle";
import {compose} from "../utils/compose";

export type PlayerInfoProps = {
    imageIndex: number;
    isActive?: boolean;
    id?: any;
    score?: number;
    pos?: PositionType;
};

export function PlayerInfoPure(
    {
        imageIndex,
        isActive = false,
        score,
        pos,
    }: PlayerInfoProps,
) {
    const [isInPosition, setIsInPosition] = useState(false);

    useEffect(() => {
        setIsInPosition(true);
    }, []);

    const userImageSize = useVPStyle({
        width: 100,
        height: 100,
    });
    const userScoreSize = useVPStyle({
        width: 55,
        height: 63,
        fontSize: 24,
        top: -70,
        left: 24,
        paddingTop: 16,
    });

    const getPlayerPosition = (positionType: PositionType | undefined): CSSProperties => {

        const topBase = 200;
        const leftBase = 340;
        const verticalDiff = 460;
        const horizontalDiff = 280;

        switch (positionType) {

            case PositionType.top:
                return useVPStyle({
                    top: topBase + 100,
                    left: leftBase,
                });

            case PositionType.right:
                return useVPStyle({
                    top: topBase + verticalDiff,
                    left: leftBase + horizontalDiff,
                });

            case PositionType.bottom:
                return useVPStyle({
                    top: topBase + verticalDiff * 2 - 100,
                    left: leftBase,
                });

            case PositionType.left:
                return useVPStyle({
                    top: topBase + verticalDiff,
                    left: leftBase - horizontalDiff,
                });

        }

        return {};
    };

    const normalPosition = getPlayerPosition(pos);

    const getActivePosition = (
        positionType: PositionType | undefined,
        orgPosition: CSSProperties,
    ) => {
        const newPos: any = _.clone(orgPosition);
        const activeDiff = 50;

        switch (pos) {
            case PositionType.top:
                newPos.top += activeDiff;
                break;
            case PositionType.right:
                newPos.left -= activeDiff;
                break;

            case PositionType.bottom:
                newPos.top -= activeDiff;
                break;
            case PositionType.left:
                newPos.left += activeDiff;
                break;

        }

        return newPos;
    };

    const scoreInfo = score !== null ? (
        <span className="userScore" style={userScoreSize}>
            {score}
        </span>
    ) : null;

    const Player = posed.div({
        enter: normalPosition,
        active: getActivePosition(pos, normalPosition),
        inActive: normalPosition
    });

    const className = `playerInfo userImage userImage${imageIndex} ${pos} ${isActive ? 'active' : ''}`;

    return (
        <PoseGroup>
            {isInPosition && (
                <Player
                    key="player"
                    pose={isActive ? 'active' : 'inActive'}
                    className={className}
                    style={userImageSize}
                >
                    {scoreInfo}
                </Player>
            )}
        </PoseGroup>
    );
}

export const PlayerInfo = compose<PlayerInfoProps>(
    hot(module),
)(PlayerInfoPure);
