import React from 'react';
import {inject} from "mobx-react";
import {Popup} from "@web3game/common/components/popups/Popup";
import {Table} from "@web3game/common/components/Table";
import {PlayerInfo} from "@web3game/common/components";
import {GameRecord} from "@web3game/typings/openBox";
import {GamePlayerStore} from "../stores/GamePlayerStore";
import {getSortedHeroList} from "../core/gameHelper";


type GameResultPopupProps = {
    onQuit: () => void;
    gameRecord?: GameRecord;
    web3?: any;
};

const toNumber = (num: any) => {
    if (num && num.toNumber) {
        return num.toNumber();
    }

    return num;
};

export function GameResultPopupPure(
    {
        onQuit,
        gameRecord,
        web3,
    }: GameResultPopupProps,
) {

    if (!web3) {
        return null;
    }

    const heroList = !gameRecord ? [] : getSortedHeroList(gameRecord)
        .sort((a, b) => {

            if (a.score && b.score && a.score.comparedTo && b.score.comparedTo) {
                return b.score.comparedTo(a.score);
            }

            // @ts-ignore
            return a - b;
        });

    const tHead = (
        <tr>
            <th className="rankIcon">
                {" "}
            </th>
            <th className="playerIcon">
                {" "}
            </th>
            <th className="playerName">
                
            </th>
            <th className="payerScore">
                                
            </th>
            <th className="payerAward">
                
            </th>
        </tr>
    );
    
    return (
        <Popup title="" onQuit={onQuit} className="gameResultPopup">
            <Table tHead={tHead} className="gameResult">
                {heroList.map((hero, index) => (
                    <tr key={hero.id}>
                        <td className="rankIcon">
                            <div className={`rankImage rankImage${index + 1}`}>{index}</div>
                        </td>
                        <td className="playerIcon">
                            <PlayerInfo id={hero.id} imageIndex={hero.imageIndex} />
                        </td>
                        <td className="playerName">{hero.id.slice(-6)}</td>
                        <td className="payerScore">{toNumber(hero.score)}</td>
                        <td className="payerAward">{web3.fromWei(toNumber(hero.gain), 'ether')}</td>
                    </tr>
                ))}
            </Table>
        </Popup>
    );
}

type GameResultPopupInjectedProps = {
    gamePlayerStore?: GamePlayerStore;
    web3?: any;
};

export const GameResultPopup = inject(
    ({ gamePlayerStore, web3 }: GameResultPopupInjectedProps) => (
        {
            gameRecord: gamePlayerStore ? gamePlayerStore.gameRecord : null,
            web3,
        }
    ),
)(GameResultPopupPure);
