import {CSSProperties, useContext} from "react";
import _ from 'lodash';
import {ViewPortSizeContext} from "./viewPortSizeHelper";

export function useVPStyle(styles: CSSProperties) {
    const { finalRatio } = useContext(ViewPortSizeContext);
    const getStyleVal = (val: number) => (val ? val * finalRatio : 0);

    return _.reduce(
        styles,
        (result: any, value: any, key: string) => {

            if (value && _.isNumber(value)) {
                /* eslint no-param-reassign:0 */
                result[key] = getStyleVal(value);
            }

            return result;
        },
        styles,
    );
}
