import React from 'react';
import {inject, observer} from "mobx-react";
import {useVPStyle} from "@web3game/common/components/viewPort/getElStyle";
import {BoxStatus} from "../core/openBoxConstants";
import {GamePlayerStore} from "../stores/GamePlayerStore";

const AnimatedClassNames = {
    [BoxStatus.opening]: 'animated swing active',
};

type BoxInjectedStores = {
    gamePlayerStore?: GamePlayerStore;
};

type BoxProps = {
    gamePlayerStore?: GamePlayerStore;
    boxIndex: number;
    openIndex: number;
    score: number;
    boxCover: number;
};

@inject(
    (stores: BoxInjectedStores) => ({
        gamePlayerStore: stores.gamePlayerStore,
    }),
)
@observer
export class Box extends React.Component<BoxProps> {

    static defaultProps = {
        boxCover: 0,
    };

    getStatus() {
        const { boxIndex, gamePlayerStore } = this.props;
        const isOpened = this.isBoxOpened();
        const isOpening = gamePlayerStore && gamePlayerStore.openingBox
            && (gamePlayerStore.openingBox.boxIndex === boxIndex);

        if (isOpened) {
            return BoxStatus.opened;
        }

        return isOpening ? BoxStatus.opening : BoxStatus.closed;
    }

    getAnimatedClassByStatus(status: string) {
        const { gamePlayerStore } = this.props;
        if (!gamePlayerStore || gamePlayerStore.isPaused) {
            return '';
        }

        return AnimatedClassNames[status] || '';
    }

    isBoxOpened = () => {
        const { openIndex, gamePlayerStore } = this.props;
        if (!gamePlayerStore) {
            return false;
        }

        return openIndex <= gamePlayerStore.finishedOpenIndex;
    };

    render() {
        const { score, boxCover } = this.props;
        const isBoomer = score === 0;
        const status = this.getStatus();
        const boomClassName = isBoomer ? 'boom' : '';
        const scoreClassName = score >= 5 ? 'largerThen5' : 'lessThen5';
        const boxCoverClassName = `box${boxCover}`;
        const boxClassName = `${boomClassName} ${scoreClassName} score score${score}`;

        const boxWrapperClassName = `${boxCoverClassName}`;
        const boxSize = useVPStyle({
            width: 100,
            height: 132,
        });

        return (
            <div
                className={`box box-${status} ${boxWrapperClassName} ${this.getAnimatedClassByStatus(status)} xk`}
                style={boxSize}
            >
                {!this.isBoxOpened() ? null : (
                    <div className={boxClassName}>
                        {isBoomer ? null : ` `}
                    </div>
                )}
            </div>
        );
    }
}
