import React from 'react';
import {inject} from "mobx-react";
import {getLang} from "@web3game/lang";
import {GameUIStore} from "../../stores/GameUIStore";
import {GameUIScene} from "../../utils/constants";
import {useVPStyle} from "../viewPort/getElStyle";

type QuitGameButtonProps = {
    onQuitPlayer?: () => void;
    onQuitGame?: () => void;
    isPlayerActive: boolean;
};

export function QuitGameButtonPure(
    {
        onQuitPlayer,
        onQuitGame,
        isPlayerActive,
    }: QuitGameButtonProps,
) {
    const onClick = isPlayerActive ? onQuitPlayer : onQuitGame;

    if (!onClick) {
        return null;
    }

    const buttonSize = useVPStyle({
        paddingTop: 16,
        fontSize: 28,
        width: 280,
        height: 120,
    });


    return (
        <button
            className="gameQuitButton"
            onClick={onClick}
            type="button"
            style={buttonSize}
        >
            {getLang().BACK}
        </button>
    );
}

QuitGameButtonPure.defaultProps = {
    isPlayerActive: false,
};

type QuitGameButtonInjectedProps = {
    gameUIStore?: GameUIStore;
    onQuitGame: () => void;
    onQuitPlayer: () => void;
};

export const QuitGameButton = inject(
    (
        { gameUIStore }: QuitGameButtonInjectedProps,
    ) => {
        const isPlayerActive = gameUIStore && gameUIStore.scene === GameUIScene.gamePlayer;

        return {
            isPlayerActive,
        };
    },
)(QuitGameButtonPure);
