import React from 'react';
import {getLang} from "@web3game/lang";
import {useVPStyle} from "../viewPort/getElStyle";

type HistoryButtonProps = {
    onShowHistory: () => void;
};

export function HistoryButton(
    {
        onShowHistory,
    }: HistoryButtonProps,
) {
    const buttonSize = useVPStyle({
        paddingTop: 16,
        fontSize: 28,
        width: 280,
        height: 120,
    });

    return (
        <button
            className="gameHistoryButton"
            onClick={onShowHistory}
            type="button"
            style={buttonSize}
        >
            {getLang().OPEN_BOX_PAGE.HISTORY}
        </button>
    );
}

export default React.memo(HistoryButton);
