import React from 'react';
import {inject} from "mobx-react";
import {GameUIStore} from "../stores/GameUIStore";
import {GameUIScene} from "../utils/constants";

type ScenePureProps = {
    name: GameUIScene;
    isActive?: boolean;
    children: () => React.ReactElement;
};

export function ScenePure(
    {
        isActive,
        children,
    }: ScenePureProps,
) {
    if (!isActive) {
        return null;
    }

    return children();
}

type SceneInjectorStores = { gameUIStore: GameUIStore };

export const Scene = inject(
    ({ gameUIStore }: SceneInjectorStores, { name }) => ({
        isActive: gameUIStore.scene === name,
    }),
)(ScenePure);
