import BigNumber from "bignumber.js";

export type GameRecord = {
    id?: any;
    currentUserId: any;
    play: boolean;
    scores: number[];
    sequencs: number[];
    playerScores: BigNumber[];
    playerAwards: BigNumber[];
    playerAddrs: string[];
};

export type BetEvent = {
    id?: number;
    issueNo: number;
    money: number;
    time: number;
    currentUserId: string;
}

// export type PositionType = 'top' | 'bottom' | 'left' | 'right';

export enum PositionType {
    top = 'top',
    bottom = 'bottom',
    left = 'left',
    right = 'right'
}

export type PlayerScoreByPosition = {
    top: number;
    bottom: number;
    left: number;
    right: number;
};

export type BoxInfo = {
    boxIndex: number;
    score: number;
    openIndex: number;
    heroId: any;
    heroIndex: number;

    /** @type {HeroInfo} */
    heroInfo: any;
    heroPos: any;
};

export type HeroInfo = {
    id: any;
    index: number;
    pos: any;
    isCurrentUser: boolean;
    score: BigNumber;
    imageIndex: number;
    gain: BigNumber;
};
