import _ from 'lodash';
import {Howl} from 'howler';
import {BoxInfo, GameRecord, HeroInfo, PositionType} from "@web3game/typings/openBox";


export const Sounds = {
    boom: require('../statics/sounds/oh_no.mp3'),
    addScore: require('../statics/sounds/add-score.mp3'),
    result: require('../statics/sounds/result.wav'),
};

export function playSound(soundSrc: string) {
    const sound = new Howl({
        src: [soundSrc],
    });

    sound.play();

}

export function getSortedHeroList(gameRecord: GameRecord) {
    const targetSelfPosition = 2;
    const {currentUserId, playerAddrs, playerScores, playerAwards = []} = gameRecord;

    const currentUserIdIndex = playerAddrs.indexOf(currentUserId);
    const imageIndexsMap = getRandomUsersImageIndex(playerAddrs);

    let resortedUserIds: string[] = [];

    const posDiff = currentUserIdIndex - targetSelfPosition;

    if (posDiff > 0) {
        (() => {
            for (let i = posDiff; i < playerAddrs.length; i++) {
                resortedUserIds.push(
                    playerAddrs[i],
                );
            }

            for (let i = 0; i < posDiff; i++) {
                resortedUserIds.push(playerAddrs[i]);
            }
        })();
    } else if (posDiff < 0) {
        (() => {
            const startMoveIndex = playerAddrs.length + posDiff;
            for (let i = startMoveIndex; i < playerAddrs.length; i++) {
                resortedUserIds.push(playerAddrs[i]);
            }

            for (let i = 0; i < startMoveIndex; i++) {
                resortedUserIds.push(playerAddrs[i]);
            }
        })();
    } else {
        resortedUserIds = _.cloneDeep(playerAddrs);
    }

    /** @returns {HeroInfo} */
    const genOtherRecord = (pos: PositionType, sortedIndex: number): HeroInfo => {
        const id = resortedUserIds[sortedIndex];
        const index = playerAddrs.indexOf(id);
        return {
            id,
            pos,
            index,
            isCurrentUser: false,
            score: playerScores[index],
            gain: playerAwards[index],
            imageIndex: imageIndexsMap[id],
        };
    };

    /** @returns {HeroInfo} */
    const getCurrentRecord = (id: string): HeroInfo => {
        const index = playerAddrs.indexOf(id);

        return {
            id,
            pos: PositionType.bottom,
            index,
            isCurrentUser: true,
            score: playerScores[index],
            imageIndex: imageIndexsMap[id],
            gain: playerAwards[index],
        };
    };

    return [
        genOtherRecord(PositionType.top, 0),
        genOtherRecord(PositionType.right, 1),
        getCurrentRecord(currentUserId),
        genOtherRecord(PositionType.left, 3),
    ];
}


export function getSortedBoxes(gameRecord: GameRecord): BoxInfo[] {
    const {scores, sequencs, playerAddrs} = gameRecord;
    const sortedHeroList = getSortedHeroList(gameRecord);

    return scores.map((score: number, boxIndex: number) => {

        let openIndex = sequencs.indexOf(boxIndex);

        if (openIndex === -1) {
            openIndex = boxIndex;
        }

        const playerIndex = openIndex % 4;

        const heroId = playerAddrs[playerIndex];

        const heroInfo = _.find(sortedHeroList, hero => hero.id === heroId);

        return {
            boxIndex,
            score,
            openIndex,
            heroId,
            heroIndex: heroInfo ? heroInfo.index : -1,

            /** @type {HeroInfo} */
            heroInfo,
            heroPos: heroInfo ? heroInfo.pos : '',
        };
    });
}


const userImageIndexCache: { [name: string]: any } = {};

/**
 * returns {Object} // { userId1: 1, userId2: 9, userId3: 3, userId4: 5}
 * */
export function getRandomUsersImageIndex(userIds: string[]): { [name: string]: any } {
    const getRandomValue = () => _.random(1, 12);

    const getRandomValueNotIn = (values: number[]) => {
        let value: number;

        do {
            value = getRandomValue();
        } while (values.indexOf(value) >= 0);

        return value;
    };

    return _.reduce(
        userIds,
        (im: { [name: string]: number }, userId: string) => {
            const imageIndexMap = im;

            let imageIndex = userImageIndexCache[userId];
            const prevImageIndexList = Object.values(imageIndexMap) as number[];

            if (imageIndex === undefined || prevImageIndexList.indexOf(imageIndex) >= 0) {
                imageIndex = getRandomValueNotIn(prevImageIndexList);
            }

            imageIndexMap[userId] = imageIndex;

            userImageIndexCache[userId] = imageIndex;

            return imageIndexMap;
        },
        {},
    );
}
