import React from "react";

export enum PreferMode {
    width = 'width',
    height = 'height',
}

export const initialSizeInfo = {
    perfectHeight: 0,
    perfectWidth: 0,


    preferMaxWidth: 0,
    preferMaxHeight: 0,

    winWidth: 0,
    winHeight: 0,

    widthHeightRatio: 1,

    adjustedWidth: 0,
    adjustedHeight: 0,

    finalRatio: 1,

    preferMode: PreferMode.width,
};

export type InitialSizeInfo = typeof initialSizeInfo;

export const ViewPortSizeContext = React.createContext(initialSizeInfo);


export const getWidthHeightRatio = (width: number, height: number) => width / height;

export const getWinSize = () => ({
    winHeight: window.innerHeight,
    winWidth: window.innerWidth,
});



type AdjustedSizeInfoType = Partial<InitialSizeInfo> & {
    preferMode?: PreferMode;
};

export const getAdjustedSizeInfo = (
    {
        perfectHeight = 0,
        perfectWidth = 0,

        preferMaxHeight: _preferMaxHeight,
        preferMaxWidth: _preferMaxWidth,


        winWidth = 0,
        winHeight = 0,
        preferMode = PreferMode.width,
    }: AdjustedSizeInfoType
): AdjustedSizeInfoType => {
    const widthHeightRatio = getWidthHeightRatio(perfectWidth, perfectHeight);
    let preferMaxHeight = _preferMaxHeight || perfectHeight;
    const preferMaxWidth = _preferMaxWidth || perfectWidth;

    let adjustedWidth = perfectWidth;
    let adjustedHeight = perfectHeight;
    let resizeRatioByWidth = perfectWidth / winWidth;
    let finalRatio = 1;

    if (preferMode === PreferMode.width) {
        if (preferMaxWidth < winWidth) {
            adjustedWidth = preferMaxWidth;
        } else {
            adjustedWidth = winWidth;
        }


        resizeRatioByWidth = adjustedWidth / perfectWidth;

        adjustedHeight = perfectHeight * resizeRatioByWidth;
        preferMaxHeight = perfectHeight * resizeRatioByWidth;
        finalRatio = resizeRatioByWidth;
    }


    return {
        adjustedWidth,
        adjustedHeight,

        widthHeightRatio,

        preferMaxWidth,
        preferMaxHeight,

        finalRatio,

        preferMode,
    };
};
